// app/admin/about/page.tsx
'use client';
import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { User, Calendar, Clock, MapPin, LogOut, Menu, X, Star } from 'lucide-react';

const AboutPage = () => {
  const router = useRouter();
  const [userName, setUserName] = useState('');
  const [userEmail, setUserEmail] = useState('');
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  useEffect(() => {
    // Check if user is authenticated
    const token = localStorage.getItem('token');
    if (!token) {
      router.push('/auth/login');
      return;
    }

    // Get user info from localStorage
    const name = localStorage.getItem('user_name') || 'User';
    const email = localStorage.getItem('user_email') || '';
    
    setUserName(name);
    setUserEmail(email);
  }, [router]);

  const handleLogout = () => {
    // Clear auth data
    localStorage.removeItem('token');
    localStorage.removeItem('user_id');
    localStorage.removeItem('user_name');
    localStorage.removeItem('user_email');
    
    // Redirect to login
    router.push('/auth/login');
  };

  return (
    <div className="min-h-screen bg-purple-50">
      {/* Header */}
      <header className="bg-white shadow-md">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <div className="flex items-center">
              <Star className="h-8 w-8 text-purple-600 mr-2" />
              <h1 className="text-xl font-bold text-gray-800">Parasara Jyotish</h1>
            </div>
            
            {/* Mobile menu button */}
            <div className="md:hidden">
              <button
                onClick={() => setIsMenuOpen(!isMenuOpen)}
                className="text-gray-500 hover:text-purple-600 focus:outline-none"
              >
                {isMenuOpen ? (
                  <X className="h-6 w-6" />
                ) : (
                  <Menu className="h-6 w-6" />
                )}
              </button>
            </div>
            
            {/* Desktop navigation */}
            <nav className="hidden md:flex space-x-8">
              <Link href="/admin/about" className="text-purple-600 font-medium">
                About
              </Link>
              <Link href="/admin/chat" className="text-gray-600 hover:text-purple-600">
                Chat
              </Link>
              <Link href="/admin/profile" className="text-gray-600 hover:text-purple-600">
                Profile
              </Link>
              <button
                onClick={handleLogout}
                className="text-gray-600 hover:text-red-600 flex items-center"
              >
                <LogOut className="h-4 w-4 mr-1" />
                Logout
              </button>
            </nav>
          </div>
        </div>
        
        {/* Mobile menu */}
        {isMenuOpen && (
          <div className="md:hidden">
            <div className="px-2 pt-2 pb-3 space-y-1 sm:px-3 bg-white border-t">
              <Link href="/admin/about" className="block px-3 py-2 text-purple-600 font-medium">
                About
              </Link>
              <Link href="/admin/chat" className="block px-3 py-2 text-gray-600 hover:text-purple-600">
                Chat
              </Link>
              <Link href="/admin/profile" className="block px-3 py-2 text-gray-600 hover:text-purple-600">
                Profile
              </Link>
              <button
                onClick={handleLogout}
                className="w-full text-left px-3 py-2 text-gray-600 hover:text-red-600 flex items-center"
              >
                <LogOut className="h-4 w-4 mr-1" />
                Logout
              </button>
            </div>
          </div>
        )}
      </header>

      {/* Main content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="bg-white shadow-md rounded-lg overflow-hidden">
          <div className="px-6 py-8">
            <div className="flex flex-col md:flex-row">
              <div className="md:w-1/3 flex justify-center mb-6 md:mb-0">
                <div className="flex flex-col items-center">
                  <div className="bg-purple-100 rounded-full p-6">
                    <User className="h-20 w-20 text-purple-600" />
                  </div>
                  <h2 className="mt-4 text-xl font-bold text-gray-800">{userName}</h2>
                  <p className="text-gray-600">{userEmail}</p>
                </div>
              </div>
              
              <div className="md:w-2/3 border-t md:border-t-0 md:border-l border-gray-200 pt-6 md:pt-0 md:pl-8">
                <h3 className="text-lg font-bold text-gray-800 mb-4">Welcome to Parasara Jyotish</h3>
                <p className="text-gray-600 mb-4">
                  Your personal astrological consultation platform based on traditional Vedic principles.
                </p>
                
                <div className="bg-purple-50 rounded-lg p-4 mb-4">
                  <h4 className="font-bold text-purple-800 mb-2">Your Astrological Journey</h4>
                  <p className="text-gray-700">
                    Parasara Jyotish combines ancient wisdom with modern technology to provide personalized
                    astrological insights tailored specifically to you.
                  </p>
                </div>
                
                <div className="space-y-4">
                  <div className="flex items-start">
                    <Calendar className="h-5 w-5 text-purple-600 mr-2 mt-1" />
                    <div>
                      <h4 className="font-bold text-gray-800">Birth Chart Analysis</h4>
                      <p className="text-gray-600">
                        Discover insights from your unique planetary positions at birth.
                      </p>
                    </div>
                  </div>
                  
                  <div className="flex items-start">
                    <Clock className="h-5 w-5 text-purple-600 mr-2 mt-1" />
                    <div>
                      <h4 className="font-bold text-gray-800">Timing Predictions</h4>
                      <p className="text-gray-600">
                        Learn about auspicious periods and potential challenges in your life.
                      </p>
                    </div>
                  </div>
                  
                  <div className="flex items-start">
                    <MapPin className="h-5 w-5 text-purple-600 mr-2 mt-1" />
                    <div>
                      <h4 className="font-bold text-gray-800">Astrological Consultations</h4>
                      <p className="text-gray-600">
                        Get answers to your specific questions through our AI-powered chat.
                      </p>
                    </div>
                  </div>
                </div>
                
                <div className="mt-6">
                  <Link href="/admin/chat" className="inline-flex items-center px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white font-bold rounded transition duration-150 ease-in-out">
                    Start Consultation
                  </Link>
                </div>
              </div>
            </div>
          </div>
          
          <div className="bg-gray-50 px-6 py-4 border-t border-gray-200">
            <p className="text-sm text-gray-600">
              Note: For accurate astrological readings, please ensure your birth details are complete and accurate
              in your profile.
            </p>
          </div>
        </div>
      </main>
    </div>
  );
};

export default AboutPage;